"use client";

import {
  createContext,
  useContext,
  useMemo,
  useState,
  type ReactNode,
} from "react";

export type InstallmentInterval = "monthly" | "weekly";
export type InstallmentStatus = "Active" | "Inactive";

export interface InstallmentPlan {
  id: string;
  name: string;
  amount: number;
  installments: number;
  interval: InstallmentInterval;
  startDate: string;
  endDate: string;
  status: InstallmentStatus;
}

export interface InstallmentPlanInput {
  name: string;
  amount: number;
  installments: number;
  interval: InstallmentInterval;
  startDate: string;
  status: InstallmentStatus;
}

type InstallmentPlanContextValue = {
  plans: InstallmentPlan[];
  addPlan: (plan: InstallmentPlanInput) => InstallmentPlan;
  updatePlan: (id: string, updates: InstallmentPlanInput) => InstallmentPlan | null;
  deletePlan: (id: string) => void;
};

export const calculateInstallmentEndDate = (
  startDate: string,
  installments: number,
  interval: InstallmentInterval
) => {
  if (!startDate || !installments) return startDate;
  const date = new Date(startDate);
  if (Number.isNaN(date.getTime())) return startDate;

  if (interval === "monthly") {
    date.setMonth(date.getMonth() + installments);
  } else {
    date.setDate(date.getDate() + installments * 7);
  }

  return date.toISOString().split("T")[0];
};

const INITIAL_PLANS: InstallmentPlan[] = [
  {
    id: "plan-1",
    name: "Smartphone Flex 12",
    amount: 1200,
    installments: 12,
    interval: "monthly",
    startDate: "2024-02-01",
    endDate: calculateInstallmentEndDate("2024-02-01", 12, "monthly"),
    status: "Active",
  },
  {
    id: "plan-2",
    name: "Laptop Weekly Boost",
    amount: 900,
    installments: 20,
    interval: "weekly",
    startDate: "2024-03-04",
    endDate: calculateInstallmentEndDate("2024-03-04", 20, "weekly"),
    status: "Inactive",
  },
];

const InstallmentPlanContext = createContext<InstallmentPlanContextValue | undefined>(undefined);

export const InstallmentPlanProvider = ({ children }: { children: ReactNode }) => {
  const [plans, setPlans] = useState<InstallmentPlan[]>(INITIAL_PLANS);

  const addPlan = (plan: InstallmentPlanInput): InstallmentPlan => {
    const id = crypto.randomUUID?.() ?? `plan-${Date.now()}`;
    const endDate = calculateInstallmentEndDate(plan.startDate, plan.installments, plan.interval);
    const newPlan: InstallmentPlan = {
      id,
      ...plan,
      endDate,
    };
    setPlans((prev) => [newPlan, ...prev]);
    return newPlan;
  };

  const updatePlan = (id: string, updates: InstallmentPlanInput): InstallmentPlan | null => {
    const endDate = calculateInstallmentEndDate(
      updates.startDate,
      updates.installments,
      updates.interval
    );
    let updatedPlan: InstallmentPlan | null = null;

    setPlans((prev) =>
      prev.map((plan) => {
        if (plan.id === id) {
          updatedPlan = { ...plan, ...updates, endDate };
          return updatedPlan;
        }
        return plan;
      })
    );

    return updatedPlan;
  };

  const deletePlan = (id: string) => {
    setPlans((prev) => prev.filter((plan) => plan.id !== id));
  };

  const value = useMemo(
    () => ({
      plans,
      addPlan,
      updatePlan,
      deletePlan,
    }),
    [plans]
  );

  return (
    <InstallmentPlanContext.Provider value={value}>
      {children}
    </InstallmentPlanContext.Provider>
  );
};

export const useInstallmentPlans = () => {
  const context = useContext(InstallmentPlanContext);
  if (!context) {
    throw new Error("useInstallmentPlans must be used within InstallmentPlanProvider");
  }
  return context;
};

